// Types for the billiard rating system

export interface Player {
  id: string;
  name: string;
  firstName?: string;
  lastName?: string;
  city?: string;
  yearOfBirth?: number;
  age?: number;
  rating: number;
  initialRating?: number; // –ü–æ—á–∞—Ç–∫–æ–≤–∏–π —Ä–µ–π—Ç–∏–Ω–≥ –ø—ñ—Å–ª—è –∫–∞–ª—ñ–±—Ä—É–≤–∞–Ω–Ω—è (–¥–ª—è –≥—Ä–∞—Ñ—ñ–∫–∞)
  isCMS?: boolean; // –ö–∞–Ω–¥–∏–¥–∞—Ç —É –º–∞–π—Å—Ç—Ä–∏ —Å–ø–æ—Ä—Ç—É (—Ä–µ–∞–ª—å–Ω–µ –∑–≤–∞–Ω–Ω—è)
  matches: string[]; // Match IDs
  createdAt: Date;
  updatedAt: Date;
}

export interface Match {
  id: string;
  player1Id: string;
  player2Id: string;
  player1Name?: string; // –Ü–º'—è –≥—Ä–∞–≤—Ü—è 1 (–¥–ª—è –≤—ñ—Ä—Ç—É–∞–ª—å–Ω–∏—Ö –ø—Ä–æ—Ñ—ñ–ª—ñ–≤)
  player2Name?: string; // –Ü–º'—è –≥—Ä–∞–≤—Ü—è 2 (–¥–ª—è –≤—ñ—Ä—Ç—É–∞–ª—å–Ω–∏—Ö –ø—Ä–æ—Ñ—ñ–ª—ñ–≤)
  winnerId: string;
  player1Score: number;
  player2Score: number;
  maxScore: number; // –î–æ —Å–∫—ñ–ª—å–∫–∏ –æ—á–æ–∫ –≥—Ä–∞–ª–∏ (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥, –¥–æ 5, –¥–æ 7, –¥–æ 10)
  player1RatingBefore: number;
  player2RatingBefore: number;
  player1RatingAfter: number;
  player2RatingAfter: number;
  player1RatingChange: number;
  player2RatingChange: number;
  date: Date;
  sequenceIndex?: number; // –ü–æ—Ä—è–¥–æ–∫ –æ–±—Ä–æ–±–∫–∏ –º–∞—Ç—á—É –ø—Ä–∏ —ñ–º–ø–æ—Ä—Ç—ñ (–¥–ª—è —Å–æ—Ä—Ç—É–≤–∞–Ω–Ω—è –ø—Ä–∏ –æ–¥–Ω–∞–∫–æ–≤—ñ–π –¥–∞—Ç—ñ)
  tournament?: string; // –ù–∞–∑–≤–∞ —Ç—É—Ä–Ω—ñ—Ä—É –∞–±–æ –∑–º–∞–≥–∞–Ω–Ω—è
  stage?: string; // –°—Ç–∞–¥—ñ—è –º–∞—Ç—á—É: group, round16, quarterfinal, semifinal, final
  matchWeight?: number; // –í–∞–≥–∞ –º–∞—Ç—á—É (1.0-2.0) –¥–ª—è —Ä–æ–∑—Ä–∞—Ö—É–Ω–∫—É —Ä–µ–π—Ç–∏–Ω–≥—É
}

